.payment {
  background-color: white;
}
.payment__container h1 {
  text-align: center;
  padding: 10px;
  font-weight: 400;
  background-color: rgb(234, 237, 237);
  border-bottom: 1px solid lightgray;
}
.payment__container > h1 a {
  text-decoration: none;
}
.payment__section {
  display: flex;
  padding: 20px;
  margin: 0 20px;
  border-bottom: 1px solid lightgray;
}
.payment__title {
  flex: 0.2;
}
.payment__adress,
.payment__adress,
.payment__details {
  flex: 0.8;
}
@media only screen and (max-device-width: 768px) {
  .payment__section {
    display: block;
    margin: 20px 0px;
  }
}
@media only screen and (max-device-width: 568px) {
  .payment__section {
    padding-right: 10px;
  }
}
