.home {
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  max-height: 1500px;
}
.home__row {
  display: flex;
  z-index: 1;
  margin-left: 5px;
  margin-right: 5px;
}

.home__image {
  width: 100%;
  z-index: -1;
  margin-bottom: -150px;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

@media only screen and (max-device-width: 568px) {
  .container {
    margin-top: 6rem;
  }
  .home__row .product {
    width: 83%;
  }
}
@media only screen and (max-width: 768px) {
  .container {
    margin-top: 10rem;
  }
  .home__row {
    display: block;
  }
  .home__row .product {
    width: 83%;
  }
  .home__image {
    margin-bottom: -111px;
  }
}
